/*
https://material.io/tools/color
 */
.chat .messages {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none; }

.chat .messages .message {
  clear: both;
  overflow: hidden;
  margin-bottom: 20px; }

.chat .messages .message.left .avatar {
  float: left;
  background-color: #3f51b5; }

.chat .messages .message.left .text-wrapper {
  margin-left: 20px;
  background-color: #d1d6ef; }

.chat .messages .message.left .text-wrapper::after,
.chat .messages .message.left .text-wrapper::before {
  right: 100%;
  border-right-color: #d1d6ef; }

.chat .messages .message.left .text {
  color: #3f51b5; }

.chat .messages .message.right .avatar {
  float: right;
  background-color: #ff9e80; }

.chat .messages .message.right .text-wrapper {
  float: right;
  margin-right: 20px;
  background-color: #ffece6; }

.chat .messages .message.right .text-wrapper::after,
.chat .messages .message.right .text-wrapper::before {
  left: 100%;
  border-left-color: #ffece6; }

.chat .messages .message.right .text {
  color: #e63600;
  text-align: right; }

.chat .messages .message .avatar {
  display: inline-block;
  width: 45px;
  height: 45px;
  border-radius: 50%; }

.chat .messages .message .text-wrapper {
  position: relative;
  display: inline-block;
  padding: 15px;
  min-width: calc(320px - 65px - 65px - 30px);
  max-width: calc(100% - 65px);
  border-radius: 6px; }

.chat .messages .message .text-wrapper::after,
.chat .messages .message .text-wrapper:before {
  position: absolute;
  top: 13px;
  width: 0;
  height: 0;
  border: solid transparent;
  content: ' ';
  pointer-events: none; }

.chat .messages .message .text-wrapper::after {
  margin-top: 0;
  border-width: 13px; }

.chat .messages .message .text-wrapper::before {
  margin-top: -2px;
  border-width: 15px; }

.chat .messages .message .text-wrapper .text {
  font-size: 18px; }

.chat .messages .message .text-wrapper .text .info {
  overflow: hidden;
  text-transform: uppercase;
  text-overflow: ellipsis;
  white-space: nowrap;
  letter-spacing: -1px;
  font-weight: bold;
  font-size: 80%; }

.chat .messages .message .text-wrapper .text .info time {
  font-weight: 300; }

@media (min-width: 768px) {
  .chat .messages .message .avatar {
    width: 60px;
    height: 60px; }
  .chat .messages .message .text-wrapper {
    display: inline-block;
    width: auto;
    max-width: calc(100% - 80px); } }
