/*
https://material.io/tools/color
 */
.chat {
  position: relative;
  margin-top: -1rem; }
  .chat .chat-window {
    overflow: hidden;
    padding-bottom: 71px;
    height: calc(100vh - 62px + 0.5rem); }
    .chat .chat-window .messages {
      overflow-y: scroll;
      padding-right: 0;
      height: 100%; }
      .chat .chat-window .messages::-webkit-scrollbar {
        display: none; }
    .chat .chat-window .fetch-more .btn-link {
      color: #3f51b5; }
      .chat .chat-window .fetch-more .btn-link[disabled] {
        color: inherit;
        text-decoration: none;
        cursor: auto; }
    .chat .chat-window .bottom-wrapper {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 1rem 0;
      width: 100%;
      border-top: 1px solid #d6d8db; }
    @media (min-width: 768px) {
      .chat .chat-window {
        height: calc(100vh - 62px); } }
